import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withStyles, createStyleSheet } from 'material-ui/styles';
import TextField from 'material-ui/TextField';

const styleSheet = createStyleSheet('TextFields', theme => ({
  container: {
    display: 'flex',
    flexWrap: 'wrap',
    padding: '0px 0px 20px 20px',
  },
  input: {
    fontSize: '0.8em',
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
    width: 180,
  },
}));

	/*
	 * テキストフィールドクラス
   *
	 * [props]プロパティ
	 *
	 *   textData: [
	 *     { key: 0, id: 'id', type: 'text', label: 'ラベル', value: 'バリュー', placeholder: '例）あいうえお' },
	 *   ];
	 *
	 *   ※必須、配列で指定、keyはユニークであること
	 *
	 *
	 *   onTextChange: function();
	 *   ※テキスト変更時のコールバック関数を指定
	 *
	 */
class TextFields extends Component {

	/*
	 * テキストフィールド編集時のイベント
	 * 編集したテキストをステートにセットする
	 *
	 * index: textDataの編集対象の配列番号
	 * event: 編集したイベントデータ
	 *
	 */
  handleRequestChange = index => event => {
    const textData = [...this.props.textData];
    textData[index].value = event.target.value;

// コールバックで親に渡すのでステートは更新しなくても良い
//    this.setState({ textData });

    // コールバックが指定されていれば呼び出す
    if(this.props.onTextChange){
      this.props.onTextChange(textData);
    }
  };

  render() {
    const classes = this.props.classes;
    const textData = [...this.props.textData];

    return (
      <div className={classes.container}>

        {textData.map(data => {
          return (
		        <TextField
		          key={data.key}
		          id={data.id}
		          label={data.label}
		          type={data.type}
		          className={classes.input}
		          value={data.value}
		          onChange={this.handleRequestChange(data.key)}
		          InputProps={{ placeholder: data.placeholder }}
		          marginForm
		        />
          );
        })}

      </div>
    );
  }
}

TextFields.propTypes = {
  classes: PropTypes.object.isRequired,
  textData: PropTypes.array.isRequired,
  onTextChange: PropTypes.func,
};

export default withStyles(styleSheet)(TextFields);




