import React, { Component } from 'react';
import classNames from 'classnames';
import PropTypes from 'prop-types';
import { withStyles, createStyleSheet } from 'material-ui/styles';
import keycode from 'keycode';
import Table, {
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  TableSortLabel,
} from 'material-ui/Table';
import Toolbar from 'material-ui/Toolbar';
import Typography from 'material-ui/Typography';
import Paper from 'material-ui/Paper';
import Checkbox from 'material-ui/Checkbox';
import Button from 'material-ui/Button';
import IconButton from 'material-ui/IconButton';
import DeleteIcon from 'material-ui-icons/Delete';
import FilterListIcon from 'material-ui-icons/FilterList';

/* コラップス */
import Collapse from 'material-ui/transitions/Collapse';

/* 検索フィルタテキスト */
import FilterText from './TextFields'

/* お気に入りメニュー */
import FavoriteMenu from './SelectedMenu'

/* チップ */
import Chip from './Chip'

/* アイコン */
import SearchIcon from 'material-ui-icons/Search';
import FavoriteIcon from 'material-ui-icons/Favorite';
import CloseIcon from 'material-ui-icons/Close';
import RefreshIcon from 'material-ui-icons/Refresh';
import CancelIcon from 'material-ui-icons/Cancel';

/*
 * テーブルヘッダの項目
 *
 */
const columnData = [
  { id: 'name', numeric: false, disablePadding: true, label: 'スタッフ番号' },
  { id: 'calories', numeric: false, disablePadding: false, label: '氏名' },
  { id: 'fat', numeric: false, disablePadding: false, label: '性別' },
  { id: 'carbs', numeric: false, disablePadding: false, label: '住所' },
  { id: 'protein', numeric: false, disablePadding: false, label: '業務' },
];
/*
 * EnhancedTableHead Class
 *
 * テーブルヘッダクラス
 */
class EnhancedTableHead extends Component {
  static propTypes = {
    onRequestSort: PropTypes.func.isRequired,
    onSelectAllClick: PropTypes.func.isRequired,
    order: PropTypes.string.isRequired,
    orderBy: PropTypes.string.isRequired,
  };

  createSortHandler = property => event => {
    this.props.onRequestSort(event, property);
  };

  render() {
    const { onSelectAllClick, order, orderBy } = this.props;

    return (
      <TableHead>
        <TableRow>
          <TableCell checkbox>
            <Checkbox onChange={onSelectAllClick} />
          </TableCell>
          {columnData.map(column => {
            return (
              <TableCell
                key={column.id}
                numeric={column.numeric}
                disablePadding={column.disablePadding}
              >
                <TableSortLabel
                  active={orderBy === column.id}
                  direction={order}
                  onClick={this.createSortHandler(column.id)}
                >
                  {column.label}
                </TableSortLabel>
              </TableCell>
            );
          }, this)}
        </TableRow>
      </TableHead>
    );
  }
}

/*
 * Toolbar Style
 *
 * ツールバースタイル
 */
const toolbarStyleSheet = createStyleSheet('EnhancedTableToolbar', theme => ({
  root: {
    paddingRight: 2,
  },
  highlight:
    theme.palette.type === 'light'
      ? {
          color: theme.palette.accent.A700,
          backgroundColor: theme.palette.accent.A100,
        }
      : {
          color: theme.palette.accent.A100,
          backgroundColor: theme.palette.accent[900],
        },
  spacer: {
    flex: '1 1 auto',
  },
  actions: {
    color: theme.palette.text.secondary,
  },
  title: {
    flex: '0 0 auto',
  },
  expand: {
    transform: 'rotate(0deg)',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
  right: {
    textAlign: 'right',
  },
  button: {
    margin: theme.spacing.unit,
  },
  favoriteIconSelect: {
    fill: theme.palette.primary.A700,
  },
}));
/*
 * Toolbar Class
 *
 * ツールバークラス
 */
class EnhancedTableToolbar extends Component {

  state = {
    expanded: false,
	  filterText: [
	    { key: 0, id: 'staffno', type: 'text',   label: 'スタッフ番号', value: '', placeholder: '例）12345678' },
	    { key: 1, id: 'name',    type: 'text',   label: '氏名',         value: '', placeholder: '例）派遣　花子' },
	    { key: 2, id: 'address', type: 'text',   label: '住所',         value: '', placeholder: '例）東京都台東区上野' },
	    { key: 3, id: 'nowork',  type: 'text',   label: 'おすすめ',     value: '', placeholder: '例）半年間働いていない' },
	  ],
	  // ダミー（DBから取得予定）：お気に入りリスト
	  favoriteList: [
	    { key: -1, value: '未選択' },
	    { key: 0,  value: '2017/1/1 01:01:01' },
	    { key: 1,  value: '2017/1/2 01:02:02' },
	    { key: 2,  value: '2017/1/3 01:03:03' },
	  ],
	  // ダミー（DBから取得予定）：お気に入りテキスト
	  favoriteText: [
	    [ '00000101', '新規　ひろし', '東京都台東区', '半年間働いていない' ],
	    [ '00000102', '変更　たかし', '千葉県富津市', '転職活動中' ],
	    [ '00000103', '修正　ゆうじ', '神奈川県横浜市', '半年間働いていない' ],
	  ],

	  // お気に入りリスト番号
	  favoriteListIndex: 0,
  };

  handleExpandClick = () => {
    this.setState({ expanded: !this.state.expanded });
  };

  /*
   * 検索テキストクリア時のコールバック
   *
   */
  handleResetClick = () => {
    const filterText = this.state.filterText;
    filterText.map(data => data.value = '');	// フィルタテキストクリア
    this.setState({ filterText: filterText });
	  this.setState({ favoriteListIndex: 0 });
  };

  /*
   * お気に入りに追加時のコールバック
   *
   */
  handleAddClick = () => {
    var filterText = this.state.filterText;
    var favoriteList = this.state.favoriteList;
    var favoriteText = this.state.favoriteText;
    var array = [];
    var today = new Date();
    var date = today.getFullYear() + "/" +  (today.getMonth() + 1) + "/"+ today.getDate()  + " " + today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds();
    
    filterText.map(data =>
    	array.push(data.value)
    )
    favoriteText.push(array);
    favoriteList.push({key: favoriteText.length - 1, value: date });
    this.setState({ favoriteList: favoriteList });
	  this.setState({ favoriteListIndex: favoriteText.length });	// 追加したお気に入りを選択状態にする
  };

  /*
   * 検索テキスト変更時のコールバック
   * この中で検索処理を実装
   *
   */
  callbackTextChange = (changeData) => {
    this.setState({ filterText: changeData });
    this.setState({ favoriteListIndex: 0 });
  };

  /*
   * お気に入りメニュー変更時のコールバック
   * keyを元にお気に入りのフィルタテキストをDBから取得
   *
   */
  callbackMenuChange = (index, key) => {
    // フィルタテキストをDBから取得
    // var dbValue = DB(key);
    if(key >= 0 ){
	    var favoriteText = this.state.favoriteText;	// ダミーテキスト
	    var filterText = this.state.filterText;
	    console.log('key:', key);
	    console.log('favoriteText[key]:', favoriteText[key]);
	    console.log('filterText[key]:', filterText[key]);
	    favoriteText[key].map((data, i) =>
	    	filterText[i].value = data
	    )
	    this.setState({ filterText: filterText });
    }
	  this.setState({ favoriteListIndex: index });
  };

  /*
   * お気に入り選択時のイベントハンドラ
   * 保存したテキストとリストテキストをDBから取得して検索実行
   *
   */
  handleFavouriteClick = (index) => {
    /* ******************** 実装予定 ********************** */
    /*
    var dbValue = DB(index);
    this.setState({ filterText: dbValue.textData });
    this.setState({ filterList: dbValue.listData });
    */
  };

  render() {

    const classes = this.props.classes;
    const numSelected = this.props.numSelected;

    return (
	    <div>
		    <Toolbar
		      className={classNames(classes.root, {
		        [classes.highlight]: numSelected > 0,
		      })}
		    >
		      <div className={classes.title}>
		        {numSelected > 0
		          ? <Typography type="subheading">
		              {numSelected} 項目選択
		            </Typography>
		          : <Typography type="subheading" color="secondary">スタッフ一覧</Typography>}
		      </div>
		      <div className={classes.spacer} />
		      <div className={classes.actions}>
	          {/* アイコン */}
		        {numSelected > 0
		          ? <IconButton aria-label="Delete">
		              <DeleteIcon />
		            </IconButton>
		          : <IconButton
		              className={classNames(classes.expand, {
		                [classes.expandOpen]: this.state.expanded,
		              })}
		              onClick={this.handleExpandClick}
		              aria-expanded={this.state.expanded}
		              aria-label="Filter list"
		            >
		              {this.state.expanded ? <CloseIcon /> : <FilterListIcon />}
		            </IconButton>}

		          <FavoriteMenu
		            index={this.state.favoriteListIndex}
		            icon={<FavoriteIcon className={this.state.favoriteListIndex > 0 ? classes.favoriteIconSelect : null} />}
		            listData={this.state.favoriteList}
		            onMenuChange={this.callbackMenuChange}
							/>
		          <IconButton><RefreshIcon /></IconButton>
		          <IconButton><CancelIcon onClick={this.handleResetClick} /></IconButton>
		      </div>
		    </Toolbar>

		    {/*
		      * 検索条件展開エリア
		      */}
		    <Paper>
		      <Collapse in={this.state.expanded} transitionDuration="auto">
		      	<FilterText textData={this.state.filterText} onTextChange={this.callbackTextChange} />
		      	<div className={classes.right}>
				      <Button color="primary" className={classes.button} onClick={this.handleAddClick} >
				        <FavoriteIcon />お気に入りに追加
				      </Button>
			      </div>
		      </Collapse>
		    </Paper>

		    {/*
		      * チップ
		      * unmountOnExit でコンポーネントを破棄
		      */}
				<Collapse in={!this.state.expanded} transitionDuration="auto" unmountOnExit>
					<Chip chipData={this.state.filterText} onTextChange={this.callbackTextChange} />
				</Collapse>
	    </div>
	  );
  }
};
/*
 * Toolbar Property Type
 *
 * ツールバープロパティタイプ
 */
EnhancedTableToolbar.propTypes = {
  classes: PropTypes.object.isRequired,
  numSelected: PropTypes.number.isRequired,
};
/*
 * Toolbar
 *
 * ツールバー生成
 */
EnhancedTableToolbar = withStyles(toolbarStyleSheet)(EnhancedTableToolbar);





/*
 * テーブルスタイル
 *
 */
const styleSheet = createStyleSheet('EnhancedTable', theme => ({
  paper: {
    width: '100%',
    marginTop: theme.spacing.unit * 3,
    overflowX: 'auto',
  },
}));
/*
 * ダミーテーブルデータ生成
 *
 */
let counter = 0;
function createData(name, calories, fat, carbs, protein) {
  counter += 1;
  return { id: counter, name, calories, fat, carbs, protein };
}
/*
 * EnhancedTable クラス
 * 
 * テーブルを表示する
 *
 */
class EnhancedTable extends Component {
  state = {
    order: 'asc',
    orderBy: 'calories',
    selected: [],
    data: [
      createData('0000000', '請負　花子','女', '千葉県千葉市中央区千葉１－１－１', '空港'),
      createData('0000000', '派遣　太郎','男', '千葉県千葉市中央区千葉１－１－２', 'レストラン'),
      createData('0000000', '請負　花見','女', '千葉県千葉市中央区千葉１－２－１', '倉庫'),
      createData('0000000', '請負　ハナ','女', '千葉県千葉市中央区千葉１－２－２', '病院'),
      createData('0000000', '契約　隆志','男', '千葉県千葉市中央区千葉２－１－１', '工場'),
      createData('0000000', '就業　博史','男', '千葉県千葉市中央区千葉２－１－２', 'レストラン'),
      createData('0000000', '業務　貴子','女', '千葉県千葉市中央区千葉２－２－１', '学校'),
      createData('0000000', '出向　智子','女', '千葉県千葉市中央区千葉２－２－２', '病院'),
      createData('0000000', '社員　徳一郎','男', '千葉県千葉市中央区千葉３－１－１', 'オフィス'),
      createData('0000000', '社員　徳次郎','男', '千葉県千葉市中央区千葉３－１－２', 'オフィス'),
      createData('0000000', '代理　正美','女', '千葉県千葉市中央区千葉３－２－１', 'オフィス'),
      createData('0000000', '代理　清美','女', '千葉県千葉市中央区千葉３－２－２', 'オフィス'),
    ],
  };

  handleRequestSort = (event, property) => {
    const orderBy = property;
    let order = 'desc';

    if (this.state.orderBy === property && this.state.order === 'desc') {
      order = 'asc';
    }

    const data = this.state.data.sort(
      (a, b) => (order === 'desc' ? b[orderBy] > a[orderBy] : a[orderBy] > b[orderBy]),
    );

    this.setState({ data, order, orderBy });
  };

  handleSelectAllClick = (event, checked) => {
    if (checked) {
      this.setState({ selected: this.state.data.map(n => n.id) });
      return;
    }
    this.setState({ selected: [] });
  };

  handleKeyDown = (event, id) => {
    if (keycode(event) === 'space') {
      this.handleClick(event, id);
    }
  };

  handleClick = (event, id) => {
    const { selected } = this.state;
    const selectedIndex = selected.indexOf(id);
    let newSelected = [];

    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, id);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(
        selected.slice(0, selectedIndex),
        selected.slice(selectedIndex + 1),
      );
    }

    this.setState({ selected: newSelected });
  };

  isSelected = id => this.state.selected.indexOf(id) !== -1;

  render() {
    const classes = this.props.classes;
    const { data, order, orderBy, selected } = this.state;

    return (
      <Paper className={classes.paper}>
        <EnhancedTableToolbar numSelected={selected.length} />
        <Table>
          <EnhancedTableHead
            order={order}
            orderBy={orderBy}
            onSelectAllClick={this.handleSelectAllClick}
            onRequestSort={this.handleRequestSort}
          />
          <TableBody>
            {data.map(n => {
              const isSelected = this.isSelected(n.id);
              return (
                <TableRow
                  hover
                  onClick={event => this.handleClick(event, n.id)}
                  onKeyDown={event => this.handleKeyDown(event, n.id)}
                  role="checkbox"
                  aria-checked={isSelected}
                  tabIndex="-1"
                  key={n.id}
                  selected={isSelected}
                >
                  <TableCell checkbox>
                    <Checkbox checked={isSelected} />
                  </TableCell>
                  <TableCell disablePadding>
                    {n.name}
                  </TableCell>
                  <TableCell numeric>
                    {n.calories}
                  </TableCell>
                  <TableCell numeric>
                    {n.fat}
                  </TableCell>
                  <TableCell numeric>
                    {n.carbs}
                  </TableCell>
                  <TableCell numeric>
                    {n.protein}
                  </TableCell>
                </TableRow>
              );
            })}
          </TableBody>
        </Table>
      </Paper>
    );
  }
}

EnhancedTable.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styleSheet)(EnhancedTable);



